import { Modal } from 'antd';
import cx from 'classnames';
import FundiinButton from 'components/Common/FundiinButton';
import { useWindowSize } from 'hooks';
import { cloneDeep } from 'lodash';
import { useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { isSmallerPc } from 'utils/helper';
import MenuMobile from './MenuMobile';
import { Brands, Buys, datatest } from './const';
// import useBrands from 'pages/Brands/hooks/useBrands';

function Category() {
  // const { data } = useBrands();
  const { t } = useTranslation();
  const [width] = useWindowSize();
  const [activeTab2, setActiveTab2] = useState(0);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [activeTabs, setActiveTabs] = useState<boolean[]>([]);

  const countFilter = useMemo(
    () => activeTabs.filter(ac => !!ac).length,
    [activeTabs]
  );

  const handleActiveTab = (id: number) => {
    const atTabs = cloneDeep(activeTabs);
    atTabs[id] = !atTabs[id];
    setActiveTabs(atTabs);
  };

  const handleActiveTab2 = (id: number) => {
    setActiveTab2(id);
  };

  const handleOpenModel = () => {
    setIsModalOpen(true);
  };

  const handleOk = (vals: boolean[]) => {
    setActiveTabs(vals);
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="container">
      <div className="category">
        <div className="category__sidebar">
          <h3 className="category__title">{t('Danh mục')}</h3>
          <div className="category__menu">
            {Brands.map((b, i) => (
              <FundiinButton
                key={i}
                className={cx([
                  activeTabs[i] ? 'active' : '',
                  isSmallerPc(width) ? 'medium' : '',
                ])}
                onClick={() => handleActiveTab(i)}
              >
                {b.icon} {t(b.text)}
              </FundiinButton>
            ))}
          </div>
          <h3 className="category__title">{t('Mua hàng')}</h3>
          <div className="category__menu">
            {Buys.map((b, i) => (
              <FundiinButton
                key={i}
                className={cx([
                  activeTab2 === i ? 'active' : '',
                  isSmallerPc(width) ? 'medium' : '',
                ])}
                onClick={() => handleActiveTab2(i)}
              >
                {t(b.text)}
              </FundiinButton>
            ))}
          </div>
        </div>
        <div className="category__result">
          <p className="category__count">123 {t('Kết quả')}</p>
          <div className="category__sidebar--mobile">
            {Buys.map((b, i) => (
              <FundiinButton
                key={i}
                className={cx(['mini', activeTab2 === i ? 'active' : ''])}
                onClick={() => handleActiveTab2(i)}
              >
                {t(b.text)}
              </FundiinButton>
            ))}
            <FundiinButton
              className={cx(['mini filter-btn', countFilter ? 'active' : ''])}
              onClick={handleOpenModel}
            >
              Danh mục {countFilter || ''}
            </FundiinButton>
          </div>
          <div className="category__list">
            {[
              ...datatest,
              ...datatest,
              ...datatest,
              ...datatest,
              ...datatest,
              ...datatest,
              ...datatest,
              ...datatest,
            ].map((d, i) => (
              <div key={i} className="category__item">
                <img className="cover-img" src={d.img} alt="item" />
                <div className="tag">
                  {d.tag.map((ta, i2) => (
                    <span key={i2}>{ta}</span>
                  ))}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      <Modal
        open={isModalOpen}
        title={t('category.Danh mục')}
        footer={<div className="ant-modal-footer__wrap"></div>}
        onCancel={handleCancel}
      >
        <MenuMobile activeTabs={activeTabs} onChange={handleOk} />
      </Modal>
    </div>
  );
}

export default Category;
